@import url('https://fonts.googleapis.com/css2?family=Mouse+Memoirs&family=Sedgwick+Ave+Display&display=swap');

:root {
  --color-white: #f0f8ff;
  --color-green: #319236;
  --color-blue: #4c51f7;
  --color-midblue: #26b4e6;
  --color-darkblue: #000074;
  --color-purple: #9d4dbb;
  --color-yellow: #f3af19;
  --color-grey: #adadad;
  
  background: linear-gradient(180deg, rgba(76,81,247,1) 0%, rgba(38,180,230,1) 35%, rgba(0,0,116,1) 100%);
}

body {
  color: var(--color-white);
  letter-spacing: 0.1em;
}

.App {
  font-family: 'Mouse Memoirs', sans-serif;
}

/* HEADER */

nav {
  display: flex;
  justify-content: space-around;
  background: var(--color-midblue);
}

.nav-links {
  width: 50%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  list-style: none;
}

nav img {
  height: 150px;
  margin: 1em;
  z-index: 100;
}

  nav img:hover {
    animation: 2s infinite alternate rockin;
  }

.nav-links li {
   color: var(--color-white);
   font-family: 'Sedgwick Ave Display', cursive;
   font-size: 3em;
   text-shadow: 2px 2px var(--color-purple);
   transition: transform 250ms;
 }

   .nav-links li:hover {
    transform: scale(1.1);
   }

.nav-links a {
   text-decoration: none;
 }

 /* MAIN */

.fetchedContainer {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  gap: 2em;
  margin: 2em 2em 22em 2em;
  text-transform: uppercase;
  min-height: 100vh;
}

.fetchedList {
  position: relative;
  transition: transform 250ms;
}

.fetchedList a {
  color: var(--color-white);
}

  .fetchedList:hover {
    transform: scale(1.3);
  }

.mainPic {
  height: 150px;
  width: 250px;
  object-fit: cover;
  border: 0.1em solid var(--color-blue);
  box-shadow: 3px 6px var(--color-purple);
}

.fetchedList h3 {
  max-width: 250px;
  text-shadow: 2px 2px 3px var(--color-darkblue);
  margin: 0;
}

  .missionPic {
    position: absolute;
    bottom: 20px;
    right: 20px;
    width: 20px;
    height: 20px;
    padding: 3px;
    border: 2px inset var(--color-white);
    border-radius: 50%;
    color: transparent;
  }

  .listname {
    position: absolute;
    top: 15px;
    left: 20px;
    font-size: 1.6em;
  }
 
  .gametype {
    position: absolute;
    bottom: 20px;
    left: 20px;
  }

    .gametype span {
      display: none;
    }

  .rating {
    position: absolute;
    bottom: 40px;
    left: 20px;
  }

  .players {
    position: absolute;
    bottom: 60px;
    left: 20px;
  }

/* DETAILED LIST */

.detailedList {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 8em 0 8em;
  text-shadow: 2px 2px 3px var(--color-darkblue);
  text-transform: uppercase;
}

.detailedContainer {
  position: relative;
  margin-top: 25px;
}

.detailedList .detailedPic {
    max-width: 780px;
    border: 0.1em solid var(--color-blue);
    box-shadow: 10px 0 20px var(--color-purple);
  }

  .detailedList .missionPic {
    width: 40px;
    height: 40px;
  }

  .detailedList .listname {
    margin: 0;
    top: 25px;
    left: 25px;
    font-size: 3.5em;
  }

  .detailedList .subname {
    position: absolute;
    bottom: 175px;
    margin: 0;
    left: 25px;
    font-size: 2em;
  }

  .detailedList h3 {
    margin: 0;
    left: 25px;
    font-size: 1.5em;
  }

  .detailedList h3 span {
    color: var(--color-grey);
  }

  .detailedList .rating {
    bottom: 25px;
  }
  
  .detailedList .players {
    bottom: 55px;
  }

  .detailedList .gametype {
    bottom: 85px;
  }

  .detailedList .maxsquad {
    position: absolute;
    left: 25px;
    bottom: 115px;
  }

  .detailedList .maxteams {
    position: absolute;
    left: 25px;
    bottom: 145px;
  }

  .detailedList .description {
    margin-top: 20px;
    padding-bottom: 333px;
    max-width: 780px;
    text-align: justify;
  } 

/* ABOUT & HOME */

.about-container {
  display: flex;
  align-items: center;
  flex-direction: column;
  height: 100vh;
  animation: 500ms flyin forwards;
}

.aboutSite {
  max-width: 780px;
  text-shadow: 2px 2px 3px var(--color-darkblue);
  text-align: justify;
  padding-top: 25px;
  margin: 0;
}

.aboutSite span {
  color: var(--color-yellow);
}

.aboutSite a {
  text-decoration: none;
  color: var(--color-yellow);
}

.about-container img {
  max-height: 440px;
  margin: 33px;
}

/* ANIMATION */

@keyframes rockin {
  from {
    transform: scale(1);
  }
  
  50% {
    transform: scale(1.1) rotate(5deg);
  }

  to {
    transform: scale(1) rotate(-5deg);
  }
}

@keyframes flyin {
  from {
    transform: translateY(-50px);
    opacity: 0;
  }

  to {
    transform: translateY(0);
    opacity: 1;
  }
}

/* MOBILE READY */

@media screen and (max-width: 870px) {

    nav {
      min-width: 320px;
    }

    nav img {
      height: 100px;
    }

    .nav-links {
      flex-direction: column;
      justify-content: space-around;
      padding: 0;
    }
    
    .nav-links li {
      font-size: 2em;
    }

    .about-container {
      font-size: 0.75em;
      margin: 0 10px;
    }

    .about-container img {
      width: 100%;
      margin: 25px 10px;
    }

    .fourCharacters {
      max-width: 425.733px;
    }

    .detailedList {
      margin: 0 25px;
    }

    .detailedList .detailedPic {
      width: 100%;
    }

    .detailedContainer .subname {
      left: 300px;
      bottom: 140px;
    }

    .detailedContainer .listname {
      font-size: 2.25em;
    }

}

@media screen and (max-width: 560px) {
  .detailedContainer {
    display: flex;
    flex-direction: column;
  }

  .detailedContainer .listname {
    position: relative;
    top: unset;
    left: unset;
    font-size: 2em;
  }

  .detailedContainer .subname,
  .detailedContainer .gametype,
  .detailedContainer .rating,
  .detailedContainer .players,
  .detailedContainer .maxteams,
  .detailedContainer .maxsquad {
    position: relative;
    top: 0;
    left: 0;
    font-size: 1.2em;
  }

  .detailedList .description {
    font-size: 1.35em;
  }
}